<template>
  <div id="home-container">
    <h1>{{ msg }}</h1>
    <div>
      {{ newWord }}
    </div>
    <div>
      {{ this.latitude }} + ' '
      {{ this.longitude }}
    </div>
    <div>
      {{ this.status }}
    </div>
    <div id="#map-link"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: 'Welcome to Vue.js App!',
      newWord: 'Something',
      geolection: 0,
      latitude: 0,
      longitude: 0,
      status: ''
    }
  },
  methods: {
    geolocation () {
      if (navigator.geolocation) {
        (function geoFindMe () {
          const mapLink = document.querySelector('#map-link')

          function success (position) {
            this.latitude = position.coords.latitude
            this.longitude = position.coords.longitude

            this.status = ''
            mapLink.href = `https://www.openstreetmap.org/#map=18/${this.latitude}/${this.longitude}`
            mapLink.textContent = `Latitude: ${this.latitude} °, Longitude: ${this.longitude} °`
          }

          function error () {
            this.status = 'Unable to retrieve your location'
          }

          if (!navigator.geolocation) {
            this.status = 'Geolocation is not supported by your browser'
          } else {
            this.status = 'Locating…'
            navigator.geolocation.getCurrentPosition(success, error)
          }
        })()
      }
    }
  },
  mounted () {
    this.geolocation()
  }
}
</script>

<style scoped>
#home-container{
  height: 100%;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



